.room-code {
  height: 40px;
  border-radius: 8px;
  overflow: hidden;
  background: var(--white);
  border: 1px solid var(--primary-color);
  cursor: pointer;
  display: flex;

  div {
    background: var(--primary-color);
    padding: 10px 12px;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  span {
    display: block;
    align-self: center;
    flex: 1;
    padding: 0 16px 0 12px;
    width: 240px;
    font-size: 12px;
    font-weight: 500;
  }
}
